@use "./breakpoints";

:root {
    --max-article-width: #{breakpoints.get-max(lg)};
    --max-jumbotron-width: #{breakpoints.get-max(xl)};

    --max-viewer-width: #{breakpoints.get-avg(xl)};
    --min-viewer-width: #{breakpoints.get-max(xs)};
    --max-info-width: var(--max-viewer-width);

    --navbar-height: 10em;
    --navbar-max-height: 8em;

    --max-book-info: 6cm;
    --min-book-info: 4cm;
}