.book-player-info {
    padding: 1em 1em;

    > div {
        display: flex;

        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
    
        max-width: var(--max-info-width);

        justify-content:space-between;
    
        gap: 1em;
    }

    .image {
        position: relative;

        margin-bottom: 0.5rem;

        @include breakpoints.sm {
            width: var(--min-book-info) !important;
        }
        @include breakpoints.lg {
            width: var(--max-book-info) !important;
        }
        
    
        img {
            display: inline-block;
            vertical-align: top;
            
            @include effects.drop-out(true);
        }
    }

    .info {
        flex-basis: 0;
        flex-grow: 1;
        flex-shrink: 1;
    
        > .title {
            margin: 0 auto;

            text-align: center;
            text-decoration: none;

            color: var(--col-black);

            @include themes.dark {
                color: var(--col-white);
            }
        }

        > hr {
            display: none;
            
            border-style: solid;
        }

        > .tags {
            display: flex;
            justify-content: center;
            flex-wrap: wrap;

            column-gap: 0.4em;
            row-gap: 0.4em;

            margin-top: 0.5rem;
            margin-bottom: 1rem;
            // border-top: var(--line-width) solid;

            > * {
                flex-shrink: 0;
                flex-grow: 1;

                border: var(--line-width) solid;
                // padding-top: 2px;
                // padding-bottom: 4px;
                padding: 2px 1em;

                // margin-top: -2px;
                // padding-top: 2px;;

                font-weight: bold;
                text-align: center;
                text-decoration: none;
            }
        }

        > .description {
            // margin-block-end: 0;
            // width: calc(100% - 1em);
            // max-width: breakpoints.get-min(lg);
            max-width: 75ch;
        }
    }
    
    @include breakpoints.sm {
        > div {
            flex-direction: row !important;
            justify-content: center !important;
            align-items:flex-start !important;
        }

        .title {
            text-align: left !important;

            margin-bottom: -2px !important;
            border: var(--line-width) solid !important;
            padding: 0.5rem !important;
        }

        .info > hr {
            // display: block !important;
            // margin-bottom: calc(var(--line-width) * -1) !important;
        }

        .image {
            margin-bottom: 0 !important;

            > img {
                float: right !important;

                // width: 3cm !important;
            }
        }

        .tags {
            justify-content: flex-start !important;

            column-gap: 0.5em !important;
            row-gap: 0.25em !important;

            margin-top: 0 !important;
            margin-bottom: 1em !important;

            padding-left: 1em;
            padding-right: 1em;

            // border-top: var(--line-width) solid;

            > * {
                flex-grow: 0 !important;
            }
        }

        .description {
            padding-left: 1em;
        }
    }
}
